<template>
  <div id="app">
    <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App" />
    <h1>{{ message }}</h1>
    <div>
      <ul>
        <li v-for='chapter in chapterList' v-bind:key='chapter.chapterId'>
          <span>
            {{ chapter.chapterTitle }}
          </span>
          <span>
            {{ chapter.timer }}
          </span>
          <button @click="delChapter(chapter.chapterId)">删除</button>
        </li>
      </ul>

      <input type="text" v-model="chapter" />
      <!-- v-model进行数据双向绑定 -->
      <button @click="addChapter">添加新闻数据</button>
    </div>
    <button @click="sendData">submit</button>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import axios from 'axios'

export default {
  name: 'App',

  components: {
    HelloWorld
  },

  data() {
    return {
      message: "",
      chapterList: [],
      chapter: ''
    }
  },
  // 页面中调用的方法，或者程序实现逻辑中调用的方法
  methods: {
    sendData() {
      console.log('submit');
      axios.post('http://localhost:3000/postdata', { name: 'zs', id: 211 })
        .then(res => {
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
    },
    getList() {
      axios.get('http://localhost:3000/getlist')
        .then(res => {
          console.log(res);
          this.chapterList = res.data;
        })
    },
    addChapter() {
      axios.post('http://localhost:3000/sub', {
        chapterTitle: this.chapter,
        timer: new Date()
      }).then(res => {
        console.log(res);
        this.chapterList = res.data
      })
    },
    delChapter(id) {
      axios.post('http://localhost:3000/del', { id: id })
        .then(res => {
          console.log(res);
          this.chapterList = res.data
        })
    }
  },
  // 生命周期函数--钩子函数一般在初始化页面完成后，再对DOM节点进行相关操作
  mounted() {
    axios.get("http://localhost:3000/getdata").then(res => {
      this.message = res.data.title;
      console.log(res);
    })
    this.getList()
  }
}
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

li {
  list-style: none;
}
</style>
